body {
  height: 100%;
  color: var(--color-black);
  background-color: var(--color-white);
  margin: 0;
  font-family: Montserrat Alternates, Inter, sans-serif;
  font-weight: 300;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  width: 0;
  height: 0;
}

* {
  box-sizing: border-box;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 13px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-dark-grey: #1c1c1c;
  --light-grey: rgba(28, 28, 28, .2);
  --main-blue: #4753dc;
  --light-blue: #f1f2fe;
  --yellow: #ffdc75;
  --white: #fff;
  --main-trans: 250ms cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding-top: 22px;
  padding-bottom: 17px;
  display: flex;
}

.header-logo-link {
  width: 49px;
  height: 27px;
  margin-left: 11px;
  margin-right: 42px;
}

.icon-logo-mob {
  width: 49px;
  height: 27px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

.icon-logo-pc {
  display: none;
}

@media screen and (max-width: 389px) {
  .button-container {
    width: 100%;
    max-width: 265px;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    display: flex;
  }
}

.button-container {
  width: 100%;
  max-width: 265px;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  display: flex;
}

.link-to-page2 {
  width: 93px;
  height: 25px;
  border-radius: 100px;
}

.button {
  background-color: var(--main-blue);
  color: var(--light-blue);
  text-transform: uppercase;
  transition: all var(--main-trans);
  border: none;
  border-radius: 100px;
  outline: none;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 4px 7px;
  font-family: Montserrat Alternates;
  font-size: 14px;
  font-weight: 700;
  line-height: normal;
  display: flex;
}

.button:hover, .button:focus {
  background-color: var(--light-blue);
  color: var(--main-blue);
  transition: all var(--main-trans);
}

.news-button {
  max-width: 93px;
  padding: 4px 13px;
}

.load-button {
  max-width: 159px;
}

.icon-book-mob {
  width: 19px;
  height: 9px;
  stroke-width: 0;
  display: inline-block;
}

.icon-open-book {
  stroke: var(--white);
  fill: var(--white);
  transition: all var(--main-trans);
}

.button:hover .icon-open-book, .button:focus .icon-open-book {
  stroke: var(--main-blue);
  fill: var(--main-blue);
  transition: all var(--main-trans);
}

.icon-book-pc {
  display: none;
}

