.main-container {
  position: relative;
}

.main-container:before, .main-container:after {
  content: "";
  width: 100%;
  height: 200px;
  z-index: 1;
  pointer-events: none;
  background: linear-gradient(rgba(255, 255, 255, 0) 33.33%, #fff 100%);
  border-radius: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main-content {
  max-height: 1512px;
  width: 100%;
  max-width: 768px;
  background-color: var(--light-blue);
  border-radius: 20px;
  padding: 20px 28px 0;
  position: relative;
  overflow-y: auto;
}

.title {
  color: var(--main-dark-grey);
  text-align: justify;
  margin-bottom: 12px;
  font-family: Montserrat Alternates;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.container-for-picture {
  width: 100%;
  height: auto;
  border-radius: 16px 16px 0 0;
  margin-bottom: 11px;
}

.main-picture {
  width: 100%;
  height: auto;
  border-radius: 16px 16px 0 0;
}

.container-for-text {
  width: 100%;
}

.text-list-item {
  margin-bottom: 12px;
}

.text-list li:nth-last-child(1) {
  margin-bottom: 95px;
}

.text {
  color: var(--main-dark-grey);
  text-align: justify;
  font-family: Montserrat Alternates;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 1.125;
}

.main-social-media {
  display: none;
}

