.backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  transition: opacity var(--main-trans), visibility var(--main-trans);
  background-color: rgba(46, 47, 66, .4);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .15s;
}

.modal {
  width: 272px;
  background-color: var(--white);
  transition: transform var(--main-trans), opacity var(--main-trans);
  border-radius: 20px;
  padding: 54px 21px 17px;
  transition-delay: .15s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 33px rgba(0, 0, 0, .07);
}

.modal-close-btn {
  width: 26px;
  height: 26px;
  cursor: pointer;
  background-color: var(--white);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 21px;
}

.modal-close-svg {
  stroke-width: 0;
  width: 26px;
  height: 26px;
  fill: var(--light-blue);
  transition: fill var(--main-trans);
  display: inline-block;
}

.modal-close-btn:hover .modal-close-svg, .modal-close-btn:focus .modal-close-svg {
  fill: var(--main-blue);
  transition: fill var(--main-trans);
}

.modal-content-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 14px;
  margin-bottom: 12px;
  display: flex;
}

.modal-content-item {
  cursor: pointer;
  background-color: var(--light-blue);
  width: 47px;
  height: 47px;
